@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap');
//  ( )  ,   
.headerMove {
    z-index: 1;
    position: absolute;
    top: 7%;
    left: 5%;
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 95%;

    .menu {
        position: relative;
        right: 2%;
        width: 40px;
        cursor: pointer;
        z-index: 2;
    }
    .sideMenu {
        position: absolute;
        top: -64px;
        left: -95px;
        width: 210px;
        height: 100vh;
        background-color: #ffffff;
        box-shadow: 2px 0 10px rgba(0, 0, 0, 0.2);
        transition: transform 0.3s ease-in-out;
        z-index: 1;
        
        .navSel {
            list-style-type: none;
             position: relative;
             top: 12%;
             left: 25%;
        }
        .navSel li, .navSel li a {
            text-decoration: none;
            color: #000000;
            font-size: 1.4rem;
            margin-bottom: 10px;
            padding-left: 20px;
            padding-top: 20px;
            margin-bottom: 20px; /*     */
        }
    }
    .homeBtn {
        position: relative;
        bottom: 8px;
        left: 25px;
        width: auto;             /*      */
        height: auto;            /*    */
        cursor: pointer;        /*     */
    
    .mainLogo {
        position: absolute;
        left: 23%;
        width: 170px;
    }
}
   

    .searchMove {
        position: relative;
        left: 38%;
        display: flex;
        justify-content: center;
        
        
        .searchInput {
            border: none;
            outline: none;
            border-bottom: 2px solid black;
            font-size: 1.8rem;
            padding-bottom: 8px;
        }
        .searchLabel {
            display: none;
        }
    }

    .memberContainer {
        position: relative;
        left: 65.5%;
        display: flex;
        flex-direction: row;
        gap: 20px;
        white-space: nowrap;
    
        .memberAll, .loginAll {
            display: flex;
            flex-direction: row;
            align-items: center;
            gap: 4px;
            cursor: pointer;
        }
        .cartBtn {
            display: flex;
            flex-direction: row;
            align-items: center;
            gap: 4px;
            text-decoration: none; //  span 
        }
        .spanText {
            font-size: 1.3rem;
            font-family: "Montserrat", sans-serif;
            font-weight: 600;
            font-style: normal;
            color: #000000;
            border-bottom: #000000;
            width: 100%;
        }
    }
}

