//  ,  : 768px-1023px
@media (min-width:768px) and (max-width:1023px){
    .logCon {
        width: 41%;
        height: 48%;

        .fieldCon {
            .cate {
                font-size: 1.8rem;
            }

            .logBtn {
                top: 280px;

                &:hover {
                    /* 390px    */
                    transform: none;
                    background-color: initial;
                    color: initial;
                    border: 1px solid #000000;;
    
                &:active {
                    background-color: #424242;
                    color: #ffffff;
                    border: none;
                }
                   
                  }
                  /*     */
                  transition: none; 
            }
        }
    }
}


