.nextButton {
    position: absolute; 
    top: 30%;
    right: 10%;
    background: rgba(0, 0, 0, 0.4);
    border-radius: 50%;
    width: 60px;
    height: 60px;
    display: flex;
    cursor: pointer;
    z-index: 1;
    
    /* hover    */
    &:hover {
        background: rgba(0, 0, 0, 0.6);
    }

    .right {
        position: absolute;
        top: 23%;
        right: 15%;
        color: rgba(255, 255, 255, 0.4);
        font-size: 30px;
        transform-origin: center;
        background: transparent; /*   */
        border: none; /*   */
        cursor: pointer;
    }
}

/* & PC : 1024px- 1279px */
@media (min-width:1024px) and (max-width:1279px){
    .nextButton {
        width: 55px;
        height: 55px;
        .right {
            position: absolute;
            top: 20%;
            right: 12%;
        }
    }
}

//  ,  : 768px-1023px
@media (min-width:768px) and (max-width:1023px){
    .nextButton {
        width: 50px;
        height: 50px;
        .right {
            position: absolute;
            top: 18%;
            right: 9%;
        }
    }
}

//  ,  : -767px*
@media (max-width:767px){
    .nextButton {
        display: none !important;
        .right {
           display: none;
        }
    }
}